import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import '../css/App.css'

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';

class JobList extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { 
      jobs: [],
      notification: false,
      run_job_props: '',
      job_name: ''
      }
  }

  async updateJobActiveState(job_name,state) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
            mutation {
              update_job_state (name:\"${job_name}\" ,state: ${state}){
             }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      console.log("user not found")
    } else {
      console.log("user  found")

    }

    const {data} = await response.json();
    return data;    
  }

  async run_etl_job(job_name,state) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
            mutation {
              run_job (name:\"${job_name}\" ,props: { key: \"\", value: \"\" }){
                name
                props {key value}
             }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      console.log("user not found")
      localStorage.removeItem("Authorization")
    } 

    const {data} = await response.json();
    return data;    
  }


  componentDidMount() {
    $('#joblist').DataTable();
  }

 show_modal(data)
  {
    var job_properties = ""
    { 
      data.map(  props =>
        job_properties  += "<b>"+ props.key + "</b>" +"=" + props.value + "<br></br>"
    )}

   {$("#jobListeModal .modal-body").html(job_properties)}
   $("#jobListeModal").modal();
  }

  set_notification(state){
    this.setState(prevState => ({
      notification: state
      }))
  }


  render(){

    var props = ''
    var notification_var = ""
    if(this.state.notification) {
      notification_var =
      <div class="alert alert-success alert-dismissible fade show" role="alert">
         <h4 class="alert-heading">Job {this.state.job_name.replace(/^"(.*)"$/, '$1')} submitted successfully!  </h4>
         <p>{this.state.run_job_props}</p>
         <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.set_notification(false)}}>
            <span aria-hidden="true">&times;</span>
         </button>
      </div>
    } 


    return (
          <div class="container-fluid">
          <div>{notification_var}</div>
          <table id="joblist" class="table table-sm table-bordered">
            <thead class="thead-dark">
                  <tr>
                    <th>Active/Inactive</th>
                    <th>Job Name</th>
                    <th>Schedule</th>
                    <th>Next Schedule</th>
                    <th>Schdule Remaining Time</th>
                    <th>Job Deploy Mode</th>
                    <th>Properties</th>
                    <th>Action</th>
                  </tr>
                </thead>
                  <tbody>
                  {this.props.jobs.map( (data) => (
                    <tr>
                      <td>{(() => {
                           if (data.is_active ) {
                             return (
                               <div className = "checkbox">
                                 <input type="checkbox"
                                     aria-describedby="UsernameHelp"
                                     data-toggle = "toggle"
                                     defaultChecked
                                     onClick={e => this.updateJobActiveState(data.name,false)}>
                                 </input>
                               </div>
                             )
                           } else {
                            return (
                              <div className = "checkbox">
                                <input type="checkbox"
                                     aria-describedby="UsernameHelp"
                                     data-toggle = "toggle"
                                     onClick={e => this.updateJobActiveState(data.name,true)}>
                                 </input>
                              </div>
                            )
                           }
                         })()}
                      </td>
                      <td>{(() => {
                           if (data.job_deploy_mode === "NA" ) {
                             return (
                               <div>
                                 <p className = "text-danger">{data.name}</p>
                               </div>
                             )
                           } else {
                            return (
                              <div>
                               {data.name}
                              </div>
                            )
                           }
                         })()}</td>
                      <td>{data.schedule}</td>
                      <td>{data.nextSchedule}</td>
                      <td>{data.schduleRemainingTime}</td>
                      <td>{data.job_deploy_mode}</td>
                      <td>
                          <button type="button" class="btn btn-secondary btn-sm" data-toggle="modal" onClick = {() => this.show_modal(data.props)}>
                            Properties
                          </button>
                          
                          <div class="modal fade" id="jobListeModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div class="modal-dialog" role="document">
                              <div class="modal-content">
                                <div class="modal-header">
                                  <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                  </button>
                                </div>
                                <div class="modal-body">
                                </div>
                                <div class="modal-footer">
                                  <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                </div>
                              </div>
                            </div>
                          </div>
                      </td>
                      <td>
                        {/* <form onSubmit={(e) => {this.run_etl_job(data.name).then(({run_job}) => {
                                console.log(run_job)
                              }); this.set_notification(true)}}> */}
                          <input type="button" value="Run" class="btn btn-secondary btn-sm" onClick = {() => {
                                 this.run_etl_job(data.name).then(({run_job}) => {
                                  console.log(JSON.parse(JSON.stringify(run_job)))
                                  this.set_notification(true)
                                  var data =  JSON.parse(JSON.stringify(run_job))
                                  for(var key in data) {
                                    {  
                                       if(key === "props") {
                                        props = JSON.stringify(data[key])
                                        this.setState(prevState => ({
                                          run_job_props: props
                                          }))
                                       } else {
                                        this.setState(prevState => ({
                                          job_name: JSON.stringify(data[key])
                                          }))
                                       }
                                    }
                                   }
                                 });
                                 
                         }}  />
                        {/* </form> */}
                        </td>
                    </tr>
                  ))}
                </tbody>
            </table>
      </div>     
    );
  }
}
export default JobList;
